@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --bg-dark: #282c34;
  --fg-light: #ffffff;
  --fg-dark: #121212;
  --green-jacque: #75FBCF;
  --shadow-light: rgba(0, 0, 0, 0.3);
}

* {
  box-sizing: border-box;
}

html, body {
  margin: 0;
  padding: 0;
  font-family: 'IBM Plex Sans', 'Helvetica', Arial, sans-serif;
  background-color: #282c34;
  background-color: var(--bg-dark);
}

.wrapper {
  display: grid;
  grid-gap: 20px;
  gap: 20px;
  grid-template-areas:
    "header"
    "content";
  height: 100vh;
  grid-template-rows: auto 1fr;
}

.main-body {
  grid-area: content;
  padding: 15px 40px;
  display: flex;
}

@media only screen and (max-width: 600px) {
  .main-body {
    padding: 15px;
  }
}

.card-deck__image, 
.drawn-card__image {
  overflow: hidden;
  border-radius: 9px;
  box-shadow: -1px 1px 3px rgba(0, 0, 0, 0.3);
  box-shadow: -1px 1px 3px var(--shadow-light);
  background-color: #ffffff;
  background-color: var(--fg-light);
}

.card-controls {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

@media only screen and (max-width: 600px) {
  .card-controls {
    display: flex;
    flex-direction: column;
    align-items: baseline;
    justify-content: flex-start;
  }

  .card-controls .control {
    width: 100%;
  }

  .card-controls .control + .control {
    margin-top: 10px;
  }
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  white-space: nowrap;
  border: 0;
}
.btn {
  background-color: var(--fg-light);
  border-radius: 15px;
  padding: 8px 16px;
  line-height: 1.2rem;
  border: 1px solid var(--fg-light);
  color: var(--fg-dark);
  transition: all 0.2s ease-in;
}

.btn:hover {
  border: 1px solid var(--fg-light);
  background-color: var(--bg-dark);
  color: var(--fg-light);
}

.btn--caution {
  background-color: #ab1212;
  color: var(--fg-light);
}
.slide-in {
  animation: slide-in 0.5s forwards;
  -webkit-animation: slide-in 0.5s forwards;
}

.slide-down {
  animation: slide-down 0.75s forwards;
  -webkit-animation: slide-down 0.75s forwards;
}

@-webkit-keyframes slide-in {
  100% { -webkit-transform: translateX(0%); transform: translateX(0%); }
}

@keyframes slide-in {
  100% { -webkit-transform: translateX(0%); transform: translateX(0%); }
}

@-webkit-keyframes slide-down {
  100% { -webkit-transform: translateY(50%); transform: translateY(50%); }
}

@keyframes slide-down {
  100% { -webkit-transform: translateY(50%); transform: translateY(50%); }
}
.body-header {
  color: var(--fg-light);
}

.body-header__title {
  font-size: 1.5em;
  margin-bottom: 5px;
}

.body-header__counter {
  margin-bottom: 5px;
}
.main-body__block--deck {
  margin-right: 20px;
}

.cards-in-deck__list {
  position: relative;
  margin: 0;
  padding: 0;
  list-style: none;
  height: 290px;
  min-width: 134px;
}

.deck-card {
  position: absolute;
  top: 0;
  left: 0;
  width: 170px;
  height: 238px;
  border-radius: 9px;
  box-shadow: -1px 1px 3px rgba(0, 0, 0, 0.1);
  background-color: var(--fg-light);
  display: flex;
  align-items: center;
  justify-content: center;
}

.card-deck {
  height: 270px;
  position: relative;
}

.draw-card {
  border-radius: 18px;
  border: 2px solid var(--fg-light);
  padding: 2px 2px 2px 8px;
  display: flex;
}

.draw-card__input {
  border: 0;
  background-color: var(--bg-dark);
  text-align: right;
  color: var(--fg-light);
  font-weight: bold;
  outline: 0;
}

.draw-card__submit {
  flex: 1 1;
}

@media only screen and (max-width: 600px) {
  .cards-in-deck__list {
    height: 220px;
  }

  .deck-card {
    width: 120px;
    height: 168px;
  }
}
.main-body__block--hand {
  flex: 1 1;
}

.cards-in-hand__list {
  margin: 0;
  padding: 0;
  position: relative;
  width: 100%;
  height: 290px;
  overflow: auto;
  list-style: none;
}

.drawn-card {
  position: absolute;
  top: 0;
  left: 0;
  -webkit-transform: translateX(-100%);
          transform: translateX(-100%);
  width: 170px;
}

@media only screen and (max-width: 600px) {
  .cards-in-hand__list {
    height: 220px;
  }

  .drawn-card {
    width: 120px;
    height: 168px;
  }
}
.notification {
  position: absolute;
  width: 20%;
  border-radius: 15px;
  background-color: var(--green-jacque);
  color: var(--fg-dark);
  top: 0;
  left: 40%;
  padding: 1rem;
  -webkit-transform: translateY(-100%);
          transform: translateY(-100%);
  box-shadow: -5px 5px 5px rgba(0, 0, 0, 0.2);
  display: none;
  z-index: 99;
}

.notification__text {
  margin: 0;
}

.notification--visible {
  display: block;
}

@media only screen and (max-width: 600px) {
  .notification {
    width: 90%;
    left: 5%;
  }
}
.main-header {
  grid-area: header;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  font-size: 1rem;
  padding: 15px 40px;
  background-color: var(--shadow-light);
}

.main-header__title {
  color: var(--green-jacque);
  font-weight: bold;
  font-size: 1.8em;
}

.main-nav__list {
  margin: 0;
  padding: 0;
  display: flex;
  list-style: none;
}

.nav-list-item {
  margin-left: 20px;
}

@media only screen and (max-width: 600px) {
  .main-header {
    padding: 15px;
  }

  .main-header__title {
    font-size: 1.4em;
  }
}
